.page {
  font-family: Roboto, Helvetica, sans-serif;

  &__content {
    display: flex;
    justify-content: center;

  }
  &__card-container {
    max-width: $card-width*4 + $card-gap-side*3;
    display: flex;
    flex-wrap: wrap;
    padding: $container-padding-top 0 $container-padding-bottom;
  }
}
